.codehilite {
  margin: 2em 0 2.8em;

  table, tbody {
    display: block;
    width: 100%
  }

  td {
    border: 1px solid #dee;
  }

  tr {
    display: flex;
    width: 100%;
  }

  .code {
    flex: 1 0 0px;
    margin-left: -1px;

    // firefox again
    display: block;
    max-width: calc(100% - 2em);
  }
}

pre {
  margin: .5em 0;
  line-height: 23px;
  white-space: pre;
  overflow-x: auto;
  word-break: inherit;
  word-wrap: inherit;
}

.code pre {
  padding: 0 10px;
}

.codehilite .gl {
  background: #fafafa;
  border-right: 1px solid #dddddd;
  color: #cccccc;
  user-select: none;
  padding: 0 5px;
  overflow: hidden;
}

p code, li code, td code, span > code{
  color: #666666;
  background: #fafafa;
  padding: 3px 6px;
  border-radius: 8px;
  border: 1px dotted #cccccc;
  margin: 0 4px;
  font-size: 90%;
  word-wrap: break-word;
}

a code {
  color: inherit;
  text-decoration: underline;
  margin: 0 -1px;
  padding: 3px 10px;
}

#json-response {
  margin: 2em auto 2em;
  max-width: 940px;
  padding: 1em;
  border: 1px solid rgba(255,255,255, 0.2);
  border-radius: 4px;
  background: rgba(0,0,0,0.1);
  position: relative;
  z-index: 0;

  #json-response-text {
    unicode-bidi: embed;
    font-family: monospace;
    white-space: pre;
  }

  #json-response-timing {
    position: absolute;
    bottom: .5em;
    right: .5em;
  }

  &:not(.display) {
    visibility: hidden;
  }

  &.display {
    visibility: visible;
    @extend %animated;
    @extend %fadeInDown;
  }
}
